@import url(https://fonts.googleapis.com/css2?family=Ranchers&display=swap);
@font-face {
  font-family: "Major Mono Display";
  src: url(/portfolio/static/media/MajorMonoDisplay-Regular.c38f17b4.ttf) format('truetype');
}

nav {
  background-color: transparent; 
  -webkit-backdrop-filter: blur(10px); 
          backdrop-filter: blur(10px);
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;  
  align-items: center;
  justify-content: space-between;
  padding: 0 3rem;
  transition: .35s;
}
nav #logo {
  font-family: 'Major Mono Display', monospace;
  color: #352F2F;
  font-size: 1.65rem;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
nav ul {
  display: flex;
}
nav ul li {
  list-style: none;
}
nav li a {
  color: #352F2F;
  padding: 0 1.25rem;
  transition: .35s;
}
nav li a:hover {
  color: #ca3300;
}
nav .right-nav {
  display: flex;
  align-items: center;
}

nav .menu-btn {
  display: none;
  transition: all 0.4s ease-out;
}
nav .btn-line {
  width: 25px;
  height: 3px;
  background-color: #3b3b3b;
  transition: all 0.25s ease-out;
}
nav .btn-line:nth-child(2) {
  width: 20px;
  margin-top: 6px;
  margin-bottom: 6px;
}
nav .menu-btn.close {
  transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
}
nav .menu-btn.close .btn-line:nth-child(1) {
  transform: rotate(45deg) translate(6px, 6px);
  -webkit-transform: rotate(45deg) translate(6px, 6px);
}
nav .menu-btn.close .btn-line:nth-child(2) {
  opacity: 0;
}
nav .menu-btn.close .btn-line:nth-child(3) {
  transform: rotate(-45deg) translate(7px, -7px);
  -webkit-transform: rotate(-45deg) translate(7px, -7px);
}


/*** --- Media Queries --- ***/
/* Medium Dimension */
@media only screen and (max-width: 768px) {
  nav .right-nav,
  nav .right-nav + button {
    display: none;
  }
  nav .menu-btn {
    display: block;
  }
  nav, main {
    padding: 0 2rem;
  }
}
