@font-face {
  font-family: "Outfit";
  src: url(/portfolio/static/media/Outfit-Medium.fea7d551.ttf) format('truetype');
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

::selection {
  background-color: rgba(53, 47, 47, .65);
  color: #DEC6A2;
}

::-webkit-scrollbar {
  width: 12px;
}
::-webkit-scrollbar-track {
  background:  #352F2F;
}
::-webkit-scrollbar-thumb {
  background: #DEC6A2; 
}

:root {
  --brown: #352F2F;
  --light-brown: rgba(53, 47, 47, .95);
  --orange: #ca3300;
}

html, body {
  height: 100%;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #DEC6A2;
  color: rgba(53, 47, 47, .95);
  color: var(--light-brown);
  font-family: 'Outfit', sans-serif;
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 600;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

p, span {
  color: rgba(53, 47, 47, .95);
  color: var(--light-brown);
}

a {
  text-decoration: none;
}

.a_link {
  position: relative;
  color: #ca3300;
}
.a_link::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 1px;
  background-color: #ca3300;
  -webkit-transform-origin: bottom right;
          transform-origin: bottom right;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  transition: -webkit-transform 0.5s ease;
  transition: transform 0.5s ease;
  transition: transform 0.5s ease, -webkit-transform 0.5s ease;
}
.a_link:hover::before {
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}

button {
  background-color: #DEC6A2;
  color: #352F2F;
  z-index: 1;
  position: relative;
  padding: 0.65rem 1.2rem;
  border: 2px solid #352F2F;
  outline: none;
}
button::before {
  content: '';
  z-index: -1;
  position: absolute;
  top: -1px;
  bottom: -1px;
  left: 0;
  right: 0;
  background-color: #352F2F;
  -webkit-transform-origin: center right;
          transform-origin: center right;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  transition: -webkit-transform 0.25s ease-in-out;
  transition: transform 0.25s ease-in-out;
  transition: transform 0.25s ease-in-out, -webkit-transform 0.25s ease-in-out;
}
button:hover {
  cursor: pointer;
  color: #DEC6A2;
}
button:hover::before {
  -webkit-transform-origin: center left;
          transform-origin: center left;
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}

main {
  margin: 0 auto;
  width: 100%;
  max-width: 100rem;
  min-height: 100vh;
  padding: 0 12.5rem;
}

section {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
  max-width: 62.5rem;
  min-height: 100vh;
}

section .jae_title {
  margin-bottom: 1.5rem;
}


/*** Media Queries ***/
@media only screen and (max-width: 768px) {
  nav, main {
    padding: 0 2rem;
  }
}


